.display-container {
  display: grid;
  grid-template-columns: 400px;
  justify-content: center;
  align-content: center;
  grid-template-rows: 100px;
}
.display-container input {
  background-color: rgba(255, 255, 255, 0.75);
  border: solid 1px white;
  font-size: 50px;
  color: orangered;
}
